@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500&family=Roboto:wght@300;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital@0;1&display=swap");
.testimonial {
  margin: 100px auto;
  width: 100%;
  height: auto;
}

.testimonial .testimonialTitle {
  width: 500px;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}

.testimonial .testimonialTitle img {
  margin: 30px auto;
  height: 40px;
}

.testimonial .testimonialTitle p {
  color: rgba(0, 0, 0, 0.8);
  font-family: 'Playfair Display';
  text-align: center;
}

.testimonial .person {
  display: -ms-flexbox;
  display: flex;
  width: 250px;
  margin: 50px auto;
}

.testimonial .person img {
  width: 70px;
  border-radius: 100%;
}

.testimonial .person div {
  margin: auto 15px;
}

.testimonial .person div p {
  font-size: 0.7rem;
  font-family: 'Montserrat';
  color: #54E50D;
  margin: auto 0;
}

.testimonial .person div p span {
  font-size: 0.5rem;
  color: rgba(0, 0, 0, 0.5);
}

.list-team {
  margin: auto;
  width: 1250px;
  list-style: none;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.list-team li {
  margin: auto;
}

.list-team li img {
  width: 200px;
}

.hole {
  width: 700px;
  height: 220px;
  position: relative;
}

.hole .text {
  display: -ms-flexbox;
  display: flex;
  background-color: white;
  width: 820px;
  position: absolute;
  height: 280px;
  -webkit-transform: translate(-60px, -40px);
          -ms-transform: translate(-60px, -40px);
      transform: translate(-60px, -40px);
  padding: 20px 40px;
}

.hole .text h1 {
  margin: 0;
  font-family: 'Playfair Display';
  font-weight: 100;
  font-size: 2.4rem;
  color: #B3968B;
}

.hole .text .h {
  margin: auto;
  width: 60%;
}

.minif {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: end;
      align-self: flex-end;
  cursor: pointer;
  height: 50px;
  width: 200px;
}

.minif p {
  font-size: 0.8rem;
  color: #54E50D;
  font-weight: 600;
  margin: 0;
}

.minif .line {
  height: 2px;
  width: 20px;
  margin: auto 5px;
  background: #54E50D;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.minif:hover .line {
  width: 40px;
}

.clients {
  height: 70vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.clients .clientsTitle {
  font-family: 'Montserrat';
  letter-spacing: 4px;
  font-size: 0.5rem;
  color: #B3968B;
  font-weight: 600;
  text-align: center;
}

.clients .clientsIcon {
  display: -ms-flexbox;
  display: flex;
  margin-top: 80px;
  width: 500px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.clients .clientsIcon div {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 140px;
  width: 140px;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}

.clients .clientsIcon .pt {
  background-image: url("./assets/pt2_grey.png");
  cursor: pointer;
}

.clients .clientsIcon .pt:hover {
  background-image: url("./assets/pt2.png");
}

.clients .clientsIcon .tap {
  background-image: url("./assets/tap_grey.png");
  cursor: pointer;
}

.clients .clientsIcon .tap:hover {
  background-image: url("./assets/tap.png");
}

.clients .clientsIcon .nos {
  background-image: url("./assets/nos_grey.png");
  cursor: pointer;
}

.clients .clientsIcon .nos:hover {
  background-image: url("./assets/nos.png");
}


